<template>
  <div class="selected-setlist-container" v-if="setlist">
      <div v-if="loading" class="loading"><img width="60" src="../assets/spinner.svg"></div>
      <div v-if="!loading">
        <div class="selected-setlist-header">
          <div class="row">
            <div class="col-md-12">
              <h3>{{setlist.artist.name}} @ {{setlist.venue.name}}, {{setlist.venue.city.name}}, {{setlist.venue.city.country.code}}</h3>
            </div>
          </div>
        </div>
        <div class="selected-setlist-body">
          <div class="row">
            <div class="col-md-6">
              <ul>
                <li v-for="track in tracks" v-bind:class="{'track  not-found' : track.notFound, 'track' : !track.notFound}">
                  <div class="album-img">
                    <div>{{track.playlist_order + 1}}</div>
                    <img height="50" :src="track.album.images[2].url">
                  </div>
                  <div>
                    <h5>{{track.name}}</h5>
                    <h6>{{track.album.name}}</h6>
                  </div>
                </li>
              </ul>
            </div>
            <div class="col-md-6" v-if="tracks.length > 0">
              <div v-if="tracksNotFound > 0">
                <p class="text-danger">{{tracksNotFound}} {{ tracksNotFound > 1 ? 'Tracks' : 'Track'}} not found on Spotify</p>
              </div>
              <div class="artist-image">
                <img :src="artistImage">
                <div></div>
              </div>
              <button v-if="!token" class="btn" @click="loginToSave">Login to save playlist</button>
              <button v-if="token" class="btn" @click="savePlayList">
                <div v-if="loadingSave" class="loading"><img width="40" src="../assets/spinner.svg"></div>
                <span v-if="!loadingSave">Save plalist</span>
              </button>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import Vue from 'vue';
import _ from 'lodash';
import axios from 'axios';
import VueLocalStorage from 'vue-localstorage';
Vue.use(VueLocalStorage);

export default {
  name: 'selected-setlist',
  props: ['setlist'],
  data : function () {
    return {
      tracks : [],
      loading: false,
      countLoaded: 0,
      artistImage: '',
      tracksNotFound: 0,
      token : null,
      loadingSave: false
    }
  },
  watch : {
    setlist : function() {
      this.tracks = [];
      this.countLoaded = 0;
      this.tracksNotFound = 0;
      this.artistImage;
      const artistName = this.setlist.artist.name;
      const self = this;
      self.loading = true;
      this.setlist.sets.set[0].song.forEach(function(item, index){
        self.getTracks(artistName, item.name, index);
      })
    }
  },
  beforeMount : function() {
    this.token = Vue.localStorage.get('token', null);
  },
  methods : {
    loginToSave : function() {
      const self = this;
      const url = Vue.config.BASE_API_URL + 'auth/spotify';
      window.open(url, "Login to Spotify", "width=600, height=550");
      var eventMethod = window.addEventListener ? "addEventListener" : "attachEvent";
      var eventer = window[eventMethod];
      var messageEvent = eventMethod == "attachEvent" ? "onmessage" : "message";
      // Listen to message from child window
      eventer(messageEvent,function(e) {
        // Check if origin is proper
        if( e.origin != location.origin ){ return }
        self.token = Vue.localStorage.get('token', null);
      }, false);
    },
    savePlayList : function(playlistName, userName) {
      console.log('saving');

      const self = this;

      function getTracksId() {
        const tracksId = [];
        self.tracks.map(function(item){
          tracksId.push(item.uri);
        })
        return tracksId;
      }

      const tracks = getTracksId();

      console.log(tracks);

      this.loadingSave = true;

      axios.post(Vue.config.BASE_API_URL + 'spotify/save-playlist', {
        "playlistName" : 'test omar',
        "userName": '1167004262',
        "tracks": tracks
      })
      .then(function (response) {
        console.log(response);
        self.loadingSave = false;
      })
    },
    getArtist : function(artistId) {
      var self = this;
      axios.get(Vue.config.BASE_API_URL + 'spotify/artist/'+artistId)
      .then(function (response) {
        self.artistImage = response.data.images[0].url;
      })
    },
    getTracks : function(artist, track, order) {
      if (artist && track) {
        var self = this;
        axios.get(Vue.config.BASE_API_URL + 'spotify/search/track/'+artist+'/' + track)
        .then(function (response) {
          response.data.playlist_order = order;
          if (!response.data.error) {
            self.tracks.push(response.data);
            self.countLoaded++;
            if (self.setlist.sets.set[0].song.length >= self.countLoaded) {
              self.tracks = _.orderBy(self.tracks, 'playlist_order', 'asc');
              self.loading = false;
            }

            if (self.countLoaded === 1 && self.tracks[0].artists[0].id) {
              self.getArtist(self.tracks[0].artists[0].id);
            }
          } else {
            self.tracks.push({
              notFound: true,
              playlist_order: order,
              name: track,
              album : {
                name: '',
                images : [{}, {}, {
                  url: ''
                }]
              }
            });
            self.tracksNotFound++;
          }
        })
      }
    }
  }
}
</script>

<style type="text/css">
  .selected-setlist-container {
    background-color: #f2f2f2;
    width: 66.66%;
    height: calc(100vh - 168px);
    overflow: scroll;
    position: relative;
  }

  .selected-setlist-header {
    height: 60px;
    overflow: hidden;
    padding: 0 15px;
    /*background-color: #ddd;*/
  }

  .selected-setlist-body {
    overflow: hidden;
    background-color: white;
    margin: 15px;
    padding: 15px;
  }

  .selected-setlist-body ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .artist-image {
    position: relative;
    overflow: hidden;
    line-height: 0;
  }

  .artist-image > div {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .artist-image > div:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.5;
  }

  .artist-image > img {
    position: relative;
    height: 100%;
    width: 100%;
  }

  .track {
    padding: 2px 0;
    display: flex;
  }

  .track.not-found {
    opacity: 0.5;
  }

  .track h5 {
    font-weight: bold;
  }

  .track h5, .track h6 {
    margin-top: 2px;
    margin-bottom: 4px;
  }

  .album-img {
    margin-right: 15px;
    position: relative;
    width: 50px;
    height: 50px;
  }

  .album-img > div {
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: black;
    text-align: center;
    color: white;
    height: 20px;
    width:20px;
    line-height: 21px;
    font-size: 12px;
  }

  .selected-setlist-body .btn {
    width: 100%;
    background-color: black;
    border-radius: 0;
    color: white;
    font-size: 22px;
    margin-top: 10px;
    padding: 1rem;
    font-size: 1.3em;
  }

  .selected-setlist-body .btn .loading {
    position: relative;
    transform: none;
    top: auto;
    left: auto;
  }

  .btn:hover {
    color: white;
    background-color: #312e2e;
  }
</style>


